body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	margin: 0;
  padding: 0;
}

.header {
  position: relative;
}

.navbar {
  margin-bottom: 50px;
  position: relative;
}

.menu {
  background-color: white;
  border-radius: 20px;
  padding: 1.5rem 1.5rem;
}

.menu .tab {
  text-decoration: none;
}
.main-content{
    max-width: 900px;
    margin: 220px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
}
.list{
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    max-width: 900px;
    margin: 0 auto;
}
.list .post{
    padding: 20px 0px;
    margin-top: 30px;
    margin-bottom: 10px;
    border-bottom: 1px solid rgb(67, 65, 65);
}
.list .post a{
    cursor: pointer;
}
.list .preview{
    font-weight: 50;
}
.post .title{
    font-weight: 500;
    font-size: 36px;
}
.post .more{
    margin-top: 5px;
    font-style: italic;
    font-size: 13px;
    opacity: 0.8;
}
.button button:hover {
    background-color: #0096b4;
}
.button{
    margin-top: 20px;
    position: relative;
    height: 50px;
}
.button button{
    background-color: #0085A1;
    border: 1px solid rgb(28, 115, 192) ;
    color: #fff;
    position: absolute;
    right: 15px;
    padding: 15px 20px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 800;
    letter-spacing: 1px;
    text-transform: uppercase;
    border-radius: 0;
    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
* {
	margin: 0;
	box-sizing: border-box;
	font-family: 'Roboto', sans-serif;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

.banner {
	width: 100%;
	height: 100%;
	background-position: center;
	/* background-repeat: no-repeat;  */
	/* background-size: cover; */
	/* position: absolute; */
	object-fit: cover;
	opacity: 0.5;
}

.header{
	height: 500px;
}
.header .black{
	position: absolute;
    top: 0;
    left: 0;
    background: black;
    opacity: 1;
    height: 500px;
    width: 100%;
	z-index: 1;
}
.header .navbar{
	display: flex;
	max-width: 1140px;
	margin: 0 auto;
	align-items: center;
	color: #fff;
	box-sizing: border-box;
	justify-content: space-between;
	z-index: 10;
}
.header .left{
	z-index: 1;
}

.header .logo img{
	margin-top: 20px;
	height: 20%;
	width: 20%;
	border-radius: 50%;
}
.header .right{
	z-index: 1;
}
.header .right .menu{
	display: flex;
	margin-bottom: 15px;
}
.right .tab{
	cursor: pointer;
	font-weight: 700;
	font-size: 16px;
	margin: 0 10px;
}
.right .tab:visited{
	color: black;
}
.right .tab:hover {
	color: rgb(165, 165, 165);
}
.header .content{
	margin: 100px;
	display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
	color: #fff;
}
.header .content h1{
	z-index: 1;
    font-size: 3rem;
}
.header .content p{
	z-index: 1;
    padding-top: 2rem;
    font-size: 1rem;
}
